import Bmob from "hydrogen-js-sdk";
import loading from "@/components/Loading/index.vue"
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'

let that;
let search_text;
let uid;
export default {
	components: {
		loading,
		faIcon,
		uniSegmentedControl,
		uniNavBar,
		uniIcon
	},
	data() {
		return {
			loading: true,
			current: 0,
			people: null, //获得人员数组
			is_producer: false,
			is_producer: false,
			type: '', //选择类型
		}
	},
	onLoad(options) {
		that = this;
		uid = uni.getStorageSync('uid');
	},

	onShow() {
		that.load_data()
	},
	onUnload() {

	},
	methods: {

		input_confirm(e) {
			console.log(e)
			let arr = []
			let keyWord = e.detail.value
			if(keyWord){
				for (let i = 0; i < that.people.length; i++) {
					if (that.people[i].producer.producer_name.split(keyWord).length > 1) {
						arr.push(that.people[i]);
					}
				}
				that.people = arr
			}else{
				that.load_data()
			}
			
		},

		//加载数据
		load_data(type) {
			let opreationList = []
			let thisPeople = []
			uni.showLoading({
				title: "加载中..."
			})
			const query = Bmob.Query("order_opreations");
			query.equalTo("type", "==", 1);
			query.equalTo("extra_type", "==", 1);
			query.equalTo("master", "==", uid);
			query.include("opreater", "account", "custom", "producer", "secondClass", "fristClass");
			query.statTo("sum", "debt");
			query.statTo("groupby", "producer");
			query.find().then(res => {
				uni.hideLoading()
				for(let item of res){
					if(item.producer && item.producer.objectId && item._sumDebt > 0){
						thisPeople.push(item)
					}
					
					that.people = thisPeople
				}
			})
		},

	}
}
